@-webkit-keyframes move {
	0% {
		opacity: 0;
		visibility: hidden;
		-webkit-transform: translateY(-40px);
		        transform: translateY(-40px);
	}

	100% {
		opacity: 1;
		visibility: visible;
		-webkit-transform: translateY(0);
		        transform: translateY(0);
	}
}

@keyframes move {
	0% {
		opacity: 0;
		visibility: hidden;
		-webkit-transform: translateY(-40px);
		        transform: translateY(-40px);
	}

	100% {
		opacity: 1;
		visibility: visible;
		-webkit-transform: translateY(0);
		        transform: translateY(0);
	}
}

@-webkit-keyframes left {
	0% {
		opacity: 0;
		width: 0;
	}

	100% {
		opacity: 1;
		padding: 2% 4%;
		width: 32%;
	}
}

@keyframes left {
	0% {
		opacity: 0;
		width: 0;
	}

	100% {
		opacity: 1;
		padding: 2% 4%;
		width: 32%;
	}
}



