    --container-height: 100%;
    --line-offset: calc((100% - var(--container-height))/ 2 + var(--menu-item-size));
}


.main-menu {
    display: -webkit-flex;
    display: flex;
    list-style: none;
    margin: auto 0;
    padding: 0.6em 0 0 0;
    -webkit-align-self: center;
            align-self: center;
}

.main-menu > li {
    box-sizing: border-box;
    height: var(--menu-item-size);
    width: calc(5.5 * var(--menu-item-size));  
    line-height: var(--menu-item-size);
    padding: 0 4%;
    margin: .1%;
    transition: 0.5s linear all;
    background: var(--botonInactivo);
    color: var(--botonActivo);
    cursor: pointer;
    font-size: 16px;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    font-weight: bold;
}

.main-menu > li:not(.with-submenu) {
    -webkit-clip-path: polygon(10% 0%, 0% 100%, 95% 100%, 100% 50%, 95% 0%);
            clip-path: polygon(10% 0%, 0% 100%, 95% 100%, 100% 50%, 95% 0%);
    -webkit-shape-outside: polygon(10% 0%, 0% 100%, 95% 100%, 100% 50%, 95% 0%);
            shape-outside: polygon(10% 0%, 0% 100%, 95% 100%, 100% 50%, 95% 0%);
}

.main-menu > li:nth-child(2) {
    -webkit-transform: translateX(-5%);
            transform: translateX(-5%);
}

.main-menu > li:nth-child(3) {
    -webkit-transform: translateX(-10%);
            transform: translateX(-10%)
}

.main-menu > li:nth-child(4) {
    -webkit-transform: translateX(-15%);
            transform: translateX(-15%)
}

.main-menu i {
    margin-right: 10%;
}

.main-menu > li:hover:not(.active) {
