.switch {
    position: relative;
    width: 210px;
    height: 40px;
    border: .1em solid rgb(189, 189, 189);
    color: rgb(189, 189, 189);
    font-size: 18px;
    --border-radius-size: 0.8em;
    border-radius: .9em;
}

.quality {
    display: inline-block;
    position: relative;
    width: 50%;
    height: 100%;
    line-height: 40px;
}
.quality:first-child label {
    border-radius: var(--border-radius-size) 0 0 var(--border-radius-size);
}
.quality:nth-child(2) label {
    border-radius: 0;
}
.quality:last-child label {
    border-radius: 0 var(--border-radius-size) var(--border-radius-size) 0;
}
.quality label {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    cursor: pointer;
    text-align: center;
    transition: color 0.6s, background-color 0.6s, -webkit-transform 0.6s;
    transition: transform 0.6s, color 0.6s, background-color 0.6s;
    transition: transform 0.6s, color 0.6s, background-color 0.6s, -webkit-transform 0.6s;
}
.quality input[type="radio"] {
    -webkit-appearance: none;
            appearance: none;
    width: 0;
    height: 0;
    opacity: 0;
}
.quality input[type="radio"]:focus {
    outline: 0;
    outline-offset: 0;
}
.quality input[type="radio"]:checked ~ label {
    background-color: #00acee;
    color: white;
}
.quality input[type="radio"]:active ~ label {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}


