.cb > label {
	display: inline-block;
	color: rgb(255, 0, 0);
	cursor: pointer;
	position: relative;
}
.cb > label span {
	display: inline-block;
	position: relative;
	background-color: transparent;
	width: 25px;
	height: 25px;
	-webkit-transform-origin: center;
	        transform-origin: center;
	border: 2px solid rgb(61, 61, 61);
	border-radius: 50%;
	vertical-align: -6px;
	margin-right: 10px;
	transition: background-color 150ms 200ms,
		-webkit-transform 350ms cubic-bezier(0.78, -1.22, 0.17, 1.89);
	transition: background-color 150ms 200ms,
		transform 350ms cubic-bezier(0.78, -1.22, 0.17, 1.89);
	transition: background-color 150ms 200ms,
		transform 350ms cubic-bezier(0.78, -1.22, 0.17, 1.89),
		-webkit-transform 350ms cubic-bezier(0.78, -1.22, 0.17, 1.89);
}
.cb > label span:before {
	content: "";
	width: 0px;
	height: 2px;
	border-radius: 2px;
	background: rgb(71, 71, 71);
	position: absolute;
	-webkit-transform: rotate(45deg);
	        transform: rotate(45deg);
	top: .8em;
	left: .5em;
	transition: width 50ms ease 50ms;
	-webkit-transform-origin: 0% 0%;
	        transform-origin: 0% 0%;
}
.cb > label span:after {
	content: "";
	width: 0;
	height: 2px;
	border-radius: 2px;
	background: rgb(71, 71, 71);
	position: absolute;
	-webkit-transform: rotate(305deg);
	        transform: rotate(305deg);
	top: 1em;
	left: .6em;
	transition: width 50ms ease;
	-webkit-transform-origin: 0% 0%;
	        transform-origin: 0% 0%;
}
label:hover span:before {
	width: 5px;
	transition: width 100ms ease;
}
label:hover span:after {
	width: 10px;
	transition: width 150ms ease 100ms;
}

.cb > input[type="checkbox"] {
	display: none;
}
.cb > input[type="checkbox"]:checked + label span {
	background-color: #fff;
	-webkit-transform: scale(1);
	        transform: scale(1);
}
.cb > input[type="checkbox"]:checked + label span:after {
	width: 10px;
	background: #1790b5;
	transition: width 150ms ease 100ms;
}
.cb > input[type="checkbox"]:checked + label span:before {
	width: 5px;
	background: #1790b5;
	transition: width 150ms ease 100ms;
}
.cb > input[type="checkbox"]:checked + label:hover span {
	background-color: rgb(255, 255, 255);
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
}
.cb > input[type="checkbox"]:checked + label:hover span:after {
	width: 10px;
	background: #1790b5;
	transition: width 150ms ease 100ms;
}
.cb > input[type="checkbox"]:checked + label:hover span:before {
	width: 5px;
	background: #1790b5;
	transition: width 150ms ease 100ms;
}

